.array-container 
{
    width: 100%;
    height: 100%;
}

.array-bar-container
{
    margin-top: 20px;
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
    justify-content: center;
}

.array-bar
{
    width: 12px;
    background-color: black;
    display: inline-block;
    margin: 0 1px;
}

.button-container
{
    display: flex;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 40px;
}

.button {
    background-color: black;
    color: white;
    padding:12px;
    border: 2px solid black;
    font-size: 14px;
    font-family: 'Robboto', sans-serif;
    letter-spacing: 1.5px;
    transition-duration: 0.4s;
    cursor: pointer;
    margin-right: 10px;
  }
  
.button:hover {
    background-color: white;
    border:2px solid black;
    color:black; 
}
